.app {
  text-align: center;
  background: -webkit-gradient(linear, right top, left bottom, from(#2f1008), to(#4d0909));
  background: -webkit-linear-gradient(top right, #2f1008, #4d0909);
  background: -o-linear-gradient(top right, #2f1008, #4d0909);
  background: linear-gradient(to bottom left, #2f1008, #4d0909); }

.btn {
  display: inline-block;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: #e8e8e8;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: 2px solid #282828;
  border-radius: 5px;
  padding: .4%;
  -webkit-transition: background .5s;
  -o-transition: background .5s;
  transition: background .5s; }
  .btn:hover {
    background: #5fdeb1; }

.logos {
  width: 1.5em;
  margin: 0 4%; }

.coin-table {
  border: 1px solid #1b4134;
  border-collapse: collapse;
  width: 100%;
  text-align: center; }
  .coin-table .table-head {
    padding: 10px; }
    .coin-table .table-head td {
      border-left: 1px solid #1b4134;
      border-right: 1px solid #1b4134; }
  .coin-table tr:nth-of-type(even) {
    background: rgba(176, 42, 42, 0.3); }
  .coin-table .table-body td {
    border-left: 1px solid #1b4134;
    border-right: 1px solid #1b4134;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis; }



